import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {WidgetServiceClient} from '../../../../services/widget.service.client';

@Component({
  selector: 'app-widget-text',
  templateUrl: './widget-text.component.html',
  styleUrls: ['./widget-text.component.css']
})
export class WidgetTextComponent implements OnInit {
  flag = false;
  error: string;
  alert: string;
  websiteId: string;
  pageId: string;
  widgetId: string;
  userId: string;
  widget = {};

  constructor(private widgetService: WidgetServiceClient, private router: Router, private activatedRouter: ActivatedRoute) { }

  updateWidget() {

    // if name field is undefined then set error 'flag' to true making 'error' and 'alert' message visible
    if (this.widget['name'] === undefined) {
      this.flag = true;
    } else {
      this.widgetService.updateWidget(this.widgetId, this.widget)
          .subscribe(
              (data: any) => {
                const url = '/profile/' + this.userId + '/website/' + this.websiteId + '/page/' + this.pageId + '/widget/';
                alert('Update succeed');
                this.router.navigateByUrl(url);
              },
              (error: any) => console.log(error)
          );
    }
  }

  deleteWidget() {

    // call delete widget function from widget client service
    this.widgetService.deleteWidget(this.widgetId)
        .subscribe(
            (data: any) => {
              const url = '/profile/' + this.userId + '/website/' + this.websiteId + '/page/' + this.pageId + '/widget';
              alert('Update succeed');
              this.router.navigateByUrl(url);
            },
            (error: any) => console.log(error)
        );

  }

  ngOnInit() {
    this.error = 'Enter the required field';
    this.alert = '* Enter the required fields';

    this.activatedRouter.params
        .subscribe(
            (params: any) => {
              this.websiteId = params['wid'];
              this.pageId = params['pid'];
              this.widgetId = params['wgid'];
              this.userId = params['uid'];
            }
        );

    // fetch widget values as created on widget-new component
    this.widgetService.findWidgetById(this.widgetId)
        .subscribe(
            (data: any) => this.widget = data,
            (error: any) => console.log(error)
        );
  }
}

